.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

/* .Menu {
  background-color: rgb(83, 82, 82);
  width: 100;
} */
a.Menu-Item {
  display: inline-block;
  text-decoration: none;
  color: rgb(0, 0, 0);
  font-weight: bold;
  padding: 15px 10px;
  background-color: bisque;
  border-radius: 10px;
  border: 2px solid black;
  margin: 10px;
}

a.Menu-Item:hover {
  background-color: rgb(201, 201, 201);
}

.container {
  border-radius: 10px;
  border: 1px solid black;
  width: 40%;
  margin: 20px auto;
  background-color: antiquewhite;
  /* height: 390px; */
}

/*item*/
.todo-item {
  margin: 10px 20px;
  display: flex;
  width: 365%;
  /* height: 100px; */
  border-style: solid;
  border-radius: 10px;
  border-color: black;
}

.item-star {
  display: flex;
  justify-content: center;
  align-items: center;
}

.item-status {
  margin: 0 8px;
  width: 35px;
  height: 35px;
  border-radius: 50%;
}

.item-body {
  flex: 1;
  text-align: left;
  padding: 0px 2px;
  width: 0;
}

.item-body>.todo-name {
  color: #f53030ef;
  font-size: 16px;
  margin: 5px 3px;
  font-weight: bolder;
}

.todo-time {
  color: #0a0c11;
  font-size: 16px;
  margin: 5px 3px;
  left: 0;
}

.todo-status {
  color: #868c98;
  font-size: 16px;
  margin: 5px 3px;
}

.item-end {
  margin: 5px 8px;
  display: flex;
  /* flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer; */
}

.item-icon {
  margin: 0px;
}

.form_input {
  width: 50%;
}

.todo_list_tittle {
  font-weight: bolder;
  font-size: 25px;
  margin: 0px 10px;
}

.add_todo {
  font-size: 30px;
  margin-top: 12px;
}

.form_input {
  left: 0;
  margin: 0;
  display: flex;
}

.title-timeline {
  color: aliceblue;
  font-weight: bolder;
  font-size: 40px;
}

.todo_form {
  width: 279px;
  text-align: left;
  margin: 20px auto;
}

.form_Item {
  margin: 10px 0px;
}